.node {
    width: 25px;
    height: 25px;
    outline: 1px solid gray;
    display: inline-block;
}

.node-finish {
    background-color: red;
}

.node-start {
    background-color: green;
}

.node-visited {
    -webkit-animation-name: visitedAnimation;
            animation-name: visitedAnimation;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
}

@-webkit-keyframes visitedAnimation {
    0% {
        -webkit-transform: scale(0.3);
                transform: scale(0.3);
        background-color: rgba(0, 0, 66, 0.75);
    }

    50% {
        background-color: rgba(217, 17, 187, 0.75);
    }

    75% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        background-color: rgba(34, 17, 217, 0.75);
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        background-color: rgba(0, 218, 69, 0.75);
    }
}

@keyframes visitedAnimation {
    0% {
        -webkit-transform: scale(0.3);
                transform: scale(0.3);
        background-color: rgba(0, 0, 66, 0.75);
    }

    50% {
        background-color: rgba(217, 17, 187, 0.75);
    }

    75% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        background-color: rgba(34, 17, 217, 0.75);
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        background-color: rgba(0, 218, 69, 0.75);
    }
}

.node-wall {
    background-color: rgb(12, 53, 71);
}

.node-shortest-path {
    -webkit-animation-name: shortestPath;
            animation-name: shortestPath;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
}

@-webkit-keyframes shortestPath {
    0% {
        -webkit-transform: scale(0.6);
                transform: scale(0.6);
        background-color: rgb(97, 237, 255);
    }

    50% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        background-color: rgb(97, 237, 255);
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        background-color: rgb(97, 237, 255);
    }
}

@keyframes shortestPath {
    0% {
        -webkit-transform: scale(0.6);
                transform: scale(0.6);
        background-color: rgb(97, 237, 255);
    }

    50% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        background-color: rgb(97, 237, 255);
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        background-color: rgb(97, 237, 255);
    }
}


